<template>
  <div id="about">
  </div>
</template>
<script>
import { Scene, PerspectiveCamera, OrthographicCamera, CameraHelper, Group, Mesh, SphereBufferGeometry, MeshBasicMaterial, BufferGeometry, Math as tMath, Float32BufferAttribute, Points, PointsMaterial, WebGLRenderer } from 'three'
export default {
  name: 'camera-array',
  mounted () {
    let frustumSize = 600
    let windowW = window.innerWidth
    let windowH = window.innerHeight
    let windowA = windowW / windowH

    let scene = new Scene()

    let camera = new PerspectiveCamera(50, 0.5 * windowA, 1, 10000)
    camera.position.z = 2500

    let cameraP = new PerspectiveCamera(50, 0.5 * windowA, 150, 1000)
    let cameraPH = new CameraHelper(cameraP)
    scene.add(cameraPH)

    let cameraO = new OrthographicCamera(0.5 * frustumSize * windowA / -2, 0.5 * frustumSize * windowA / 2, frustumSize / 2, frustumSize / -2, 150, 1000)
    let cameraOH = new CameraHelper(cameraO)
    scene.add(cameraOH)

    let activeCamera = cameraP
    let activeHelper = cameraPH
    cameraO.rotation.y = cameraP.rotation.y = Math.PI
    let cameraRig = new Group()
    cameraRig.add(cameraP)
    cameraRig.add(cameraO)
    scene.add(cameraRig)

    let mesh = new Mesh(
      new SphereBufferGeometry(100, 16, 8),
      new MeshBasicMaterial({ color: 0xffffff, wireframe: true })
    )

    let mesh2 = new Mesh(
      new SphereBufferGeometry(50, 16, 8),
      new MeshBasicMaterial({ color: 0x00ff00, wireframe: true })
    )
    mesh2.position.y = 150
    mesh.add(mesh2)

    let mesh3 = new Mesh(
      new SphereBufferGeometry(5, 16, 8),
      new MeshBasicMaterial({ color: 0x0000ff, wireframe: true })
    )
    mesh3.position.z = 150
    cameraRig.add(mesh3)

    let geometry = new BufferGeometry()
    let vertices = []

    for (let i = 0; i < 10000; i++) {
      vertices.push(tMath.randFloatSpread(2000))
      vertices.push(tMath.randFloatSpread(2000))
      vertices.push(tMath.randFloatSpread(2000))
    }

    geometry.addAttribute('position', new Float32BufferAttribute(vertices, 3))

    let particles = new Points(geometry, new PointsMaterial({ color: 0x888888 }))
    scene.add(particles)

    let renderer = new WebGLRenderer({ antialias: true })
    renderer.setPixelRatio(window.devicePixelRatio)
    renderer.setSize(windowW, windowH)
    document.getElementById('about').appendChild(renderer.domElement)
    renderer.autoClear = false

    function animate () {
      requestAnimationFrame(animate)
      let r = Date.now() * 0.0005
      mesh.position.set(700 * Math.cos(r), 700 * Math.sin(r), 700 * Math.sin(r))
      mesh.children[0].position.x = 70 * Math.cos(2 * r)
      mesh.children[0].position.z = 70 * Math.sin(r)
      if (activeCamera === cameraP) {
        cameraP.fov = 35 + 30 * Math.sin(0.5 * r)
        cameraP.far = mesh.position.length()
        cameraP.updateProjectionMatrix()
        cameraPH.update()
        cameraPH.visible = true
        cameraOH.visible = false
      } else {
        cameraO.far = mesh.position.length()
        cameraO.updateProjectionMatrix()
        cameraOH.update()
        cameraOH.visible = true
        cameraPH.visible = false
      }
      cameraRig.lookAt(mesh.position)
      renderer.clear()
      activeHelper.visible = false
      renderer.setViewport(0, 0, windowW / 2, windowH)
      renderer.render(scene, activeCamera)
      activeHelper.visible = true
      renderer.setViewport(windowW / 2, 0, windowW / 2, windowH)

      renderer.render(scene, camera)
    }
    animate()
  }
}
</script>
